    :global .has-float-label input:placeholder-shown:not(:focus)::-webkit-input-placeholder,
    :global .has-float-label select:placeholder-shown:not(:focus)::-webkit-input-placeholder {
      opacity: 0; }
    :global .has-float-label input:placeholder-shown:not(:focus) + *:not(.input-has-content),
