@keyframes bouncing {
  from {
    opacity: 1;
    transform: translateY(0); }
  to {
    opacity: 0.1;
    transform: translateY(-1rem); } }

.loading-bouncer {
  display: flex;
  justify-content: center; }
  .loading-bouncer > div {
    width: 1rem;
    height: 1rem;
    margin: 3rem 0.2rem;
    background: #8385aa;
    border-radius: 50%;
    animation: bouncing 0.6s infinite alternate; }
  .loading-bouncer > div:nth-child(2) {
    animation-delay: 0.2s; }
  .loading-bouncer > div:nth-child(3) {
    animation-delay: 0.4s; }
