.date-selector-container {
  position: relative;
  display: flex;
  flex-flow: wrap; }
  .date-selector-container > :global(.input-container) {
    display: flex;
    flex: 1 1 90%; }
  .date-selector-container .date-selector-button {
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: -30px;
    z-index: 1; }
    .date-selector-container .date-selector-button.clear {
      margin-left: -60px; }
      .date-selector-container .date-selector-button.clear + .date-selector-button {
        margin-left: 0; }

.date-selector-calendar-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10; }

.error-message {
  display: flex;
  flex: 1;
  padding-left: 10px;
  color: #ff0000;
  font-size: 0.8em;
  white-space: nowrap; }
