.svg-logo {
  width: 50px;
  height: 50px; }
  .svg-logo svg {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: transparent;
    padding: 0.25em 0.25em 0;
    box-sizing: border-box; }
    .svg-logo svg:not(:hover) .letter {
      opacity: 0; }
    .svg-logo svg:not(:hover) .letter.hideshow {
      animation-duration: 3s;
      animation-iteration-count: 1;
      animation-timing-function: linear;
      animation-name: hideshow; }
    .svg-logo svg:hover > .word {
      opacity: 1; }
    .svg-logo svg:hover > .letter {
      opacity: 0 !important; }

.word,
.letter {
  text-anchor: middle;
  fill: #ccc; }

.word {
  text-anchor: end;
  font-size: 0.9em;
  transition: all 1s ease-in-out;
  opacity: 0; }

.letter {
  text-anchor: middle;
  font-size: 3em;
  transition: all 1s ease-in-out; }

.diagonal {
  transform: translateX(0.6em) translateY(0.125em) rotate(-45deg); }

@keyframes hideshow {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }
