.radio {
  display: inline-flex;
  align-items: center;
  cursor: pointer; }
  .radio input[type='radio'] {
    appearance: none;
    padding-right: 2px;
    margin: 0;
    pointer-events: none; }
    .radio input[type='radio']::after {
      content: '\25ef';
      color: #aaa;
      font-size: 1.2rem;
      vertical-align: middle; }
    .radio input[type='radio']:checked::after {
      content: '\25c9';
      color: #000;
      font-size: 1.5rem; }
    .radio input[type='radio'][disabled]::after,
    .radio input[type='radio'][disabled] + span {
      color: #bbb !important; }
