.dropdown-container {
  box-shadow: 2px 2px 10px #aaa; }

.multi-select {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 100px;
  min-height: 25px;
  border: none; }

.display,
.display > input {
  cursor: pointer; }

.dropdown-container {
  position: absolute;
  top: calc(100% - 10px);
  display: none;
  width: calc(100% - 10px); }
  .dropdown-container.is-open {
    display: flex; }

.multi-select-list {
  width: 100%;
  z-index: 100; }

.separator {
  height: 1px;
  border-bottom: 1px solid;
  opacity: 0.2; }
