.application-header {
  position: fixed;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 50px;
  width: 100%;
  background-color: #000;
  color: #ccc;
  z-index: 50; }

.links-block {
  position: relative;
  height: 100%; }
  .links-block div {
    height: 100%; }
  .links-block a {
    height: 100%;
    padding: 8px;
    text-decoration: none;
    box-sizing: border-box; }

.flex-right {
  display: flex;
  justify-content: flex-end;
  align-items: center; }
