@charset "UTF-8";
.toaster {
  position: fixed;
  bottom: 12px;
  right: 12px;
  z-index: 9001;
  pointer-events: none; }

.toast {
  position: relative;
  display: flex;
  flex-direction: column;
  pointer-events: auto;
  overflow: hidden;
  margin: 0 0 6px;
  padding: 10px;
    padding-left: 40px;
  width: 300px;
  border-radius: 3px;
  box-shadow: 0 0 12px #999;
  color: #fff;
  opacity: 0;
  cursor: pointer;
  animation-name: cool-down;
  animation-duration: 3s;
  animation-iteration-count: 1; }
  .toast .title {
    font-size: 1.2rem;
    font-weight: bold; }
  .toast::before {
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
    font-size: 1.6rem; }

.toast.success {
  background-color: #00ff00;
  color: #000; }
  .toast.success::before {
    content: "✓"; }

.toast.error {
  background-color: #ff0000;
  color: #fff; }
  .toast.error::before {
    content: "!"; }

.toast.warning {
  background-color: #ff9900;
  color: #000; }
  .toast.warning::before {
    content: "⚠"; }

.toast.info {
  background-color: #00ffff;
  color: #000; }
  .toast.info::before {
    content: "ℹ"; }

@keyframes cool-down {
  0% {
    opacity: 0.8; }
  50% {
    opacity: 0.8; }
  75% {
    opacity: 0.4; }
  100% {
    opacity: 0; } }
