.input-chip {
  height: 32px;
  line-height: 32px;
  padding: 0 12px;
  border: 0;
  border-radius: 16px;
  background-color: #dedede;
  display: inline-block;
  color: rgba(0, 0, 0, 0.87);
  margin: 1px 0;
  white-space: nowrap; }
  .input-chip:global(.active),
  .input-chip:global(.active) .input-chip-delete {
    background-color: #000;
    color: #ccc; }
  .input-chip .input-chip-delete {
    display: inline-block !important;
    vertical-align: middle;
    overflow: hidden;
    text-align: center;
    height: 24px;
    width: 24px;
    background: 0 0;
    opacity: 0.54;
    cursor: pointer;
    padding: 0;
    margin: 0 0 0 4px;
    font-size: 13px;
    text-decoration: none;
    color: rgba(0, 0, 0, 0.87);
    border: none;
    outline: none; }

.input-chip-deletable {
  padding-right: 4px; }

.input-chip-text {
  display: inline-block;
  vertical-align: middle;
  font-size: 1em; }

.input-chip-clickable {
  cursor: pointer; }
