.button,
.button-link,
.button-icon {
  appearance: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: inherit;
  color: inherit;
  padding: 5px;
  border: none;
  white-space: nowrap;
  cursor: pointer; }
  .button.rounded,
  .button-link.rounded,
  .button-icon.rounded {
    border-radius: 5px; }
  .button.depress,
  .button-link.depress,
  .button-icon.depress {
    box-shadow: 0 0 5px #0f0f0f; }
    .button.depress:active,
    .button-link.depress:active,
    .button-icon.depress:active {
      box-shadow: inset 0px 0px 5px #0f0f0f; }
  .button:disabled,
  .button-link:disabled,
  .button-icon:disabled {
    background-color: #ccc !important;
    color: #666 !important;
    cursor: default; }

.button {
  min-width: 100px;
  min-height: 25px;
  text-decoration: none; }

.button-link {
  color: #0000ee;
  text-decoration: underline; }
  .button-link:focus, .button-link:active {
    color: #0000ee; }
  .button-link:hover {
    color: #3c3cff; }

.button-icon {
  flex: 0 1 0%;
  padding: 3px 6px;
  margin: 2px 5px;
  text-decoration: none; }
  .button-icon:before {
    content: attr(icon);
    font-size: 1.5rem; }
    .button-icon:before:not(:disabled) {
      cursor: pointer; }
  .button-icon.small:before {
    font-size: 0.8rem; }
