.request-indicator-container {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 3px;
  background: transparent;
  overflow: hidden;
  z-index: 10000; }

.request-indicator-loader {
  position: absolute;
  width: 200px;
  height: 100%;
  background-color: #000;
  z-index: 1;
  animation: cycle 3s infinite; }
  .request-indicator-loader.hidden {
    background-color: transparent !important; }

@keyframes cycle {
  0% {
    left: -200px; }
  100% {
    left: calc(100% + 100px); } }
