.autocomplete {
  position: relative;
  display: flex;
  flex: 1 1 100%; }

.autocomplete-menu {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
  max-height: 250px;
  padding: 5px;
  margin: 0;
  overflow: auto;
  list-style-type: none;
  z-index: 250;
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.5);
  transform: translateY(100%); }
  .autocomplete-menu .autocomplete-suggestion button {
    width: 100%; }
  .autocomplete-menu .autocomplete-suggestion-text {
    white-space: pre-line; }
  .autocomplete-menu .highlight {
    font-weight: bold; }

.no-suggestions-item {
  font-size: 13.33px;
  text-align: center; }
