.calendar:not(:global(.flat)) {
  box-shadow: 2px 2px 10px #aaa; }

.calendar {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 5px;
  z-index: 10;
  box-sizing: border-box; }
  .calendar:not(:global(.flat)) {
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translateY(100%); }
  .calendar.read-only button {
    cursor: default; }

.calendar-view-controls {
  display: flex;
  flex: 1;
  justify-content: space-between;
  align-items: center; }

.view-shift-button {
  padding: 3px 15px; }
  .view-shift-button:hover {
    background-color: #f2f2f2; }

.calendar-view {
  display: flex;
  flex-flow: wrap; }
  .calendar-view .calendar-view-option {
    text-align: center;
    border: 1px solid transparent;
    box-sizing: border-box; }
    .calendar-view .calendar-view-option:not(.dummy-day):not(.header) {
      border-color: #efefef; }
    .calendar-view .calendar-view-option.day {
      width: calc(100% / 7); }
    .calendar-view .calendar-view-option.month {
      width: calc(100% / 3); }
    .calendar-view .calendar-view-option :global(.selected) {
      background-color: #674ea7;
      color: #fff; }
    .calendar-view .calendar-view-option.header {
      padding: 5px 0; }
    .calendar-view .calendar-view-option.dummy-day {
      z-index: -1; }
      .calendar-view .calendar-view-option.dummy-day > button {
        background-color: inherit !important; }
    .calendar-view .calendar-view-option > button {
      height: 100%;
      min-width: 100%;
      padding-top: 10px;
      padding-bottom: 10px; }
