.app-information {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 1em; }
  .app-information::before {
    content: attr(icon); }
  .app-information__detail {
    position: fixed;
    right: -100%;
    bottom: 0;
    background-color: inherit;
    padding: 5px;
    margin: 1em;
    border: 1px solid;
    pointer-events: none;
    visibility: hidden;
    transition: 1s; }
    .app-information__detail--visible {
      transition: 1s;
      visibility: visible;
      right: 0; }
