.tab-container {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 5px 0; }
  .tab-container .tabs {
    position: relative;
    height: calc(100% - 31px);
    min-width: 400px;
    border-top: 1px solid #000; }

.tab-controls {
  display: flex;
  height: 31px;
  padding: 0 5px;
  margin: 0;
  white-space: nowrap;
  list-style-type: none; }
  .tab-controls li {
    display: inline-flex;
    padding: 2px;
    margin: 0 1px;
    border-width: 1px;
    border-style: solid;
    border-color: #ccc;
    border-bottom-color: #fff;
    z-index: 10; }
  .tab-controls li:global(.active) {
    border-color: #000;
    border-bottom-color: #fff;
    margin-bottom: -1px; }
