.dropdown-menu {
  box-shadow: 2px 2px 10px #aaa; }

.dropdown-menu {
  position: fixed;
  width: 200px;
  min-height: 100px;
  background-color: #fff;
  border-radius: 4px;
  top: 0;
  right: 0;
  padding: 2px;
  margin: 0;
  list-style-type: none;
  transform: translateY(50%);
  transition: visibility 0.5s;
  transition-delay: 1s !important;
  z-index: 100; }
  .dropdown-menu .dropdown-arrow {
    position: absolute;
    top: -20px;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-bottom: 20px solid #fff;
    width: 0;
    height: 0;
    filter: drop-shadow(0 -2px 2px #aaa); }
  .dropdown-menu.left .dropdown-arrow {
    left: 0; }
  .dropdown-menu.center {
    left: 50%;
    transform: translateY(50%) translateX(-50%); }
    .dropdown-menu.center .dropdown-arrow {
      left: 50%;
      transform: translateX(-50%); }
  .dropdown-menu.right .dropdown-arrow {
    right: 0; }

.dropdown-container > input {
  display: none; }

.dropdown-container > label {
  cursor: pointer; }
  .dropdown-container > label:before {
    content: attr(icon);
    font-size: 1.5rem; }
