.mal-search-container {
  position: relative; }
  .mal-search-container.fetching .mal-clear-input,
  .mal-search-container.selected .mal-results,
  .mal-search-container.fresh .mal-results {
    visibility: hidden;
    pointer-events: none; }

.mal-search-messages {
  display: block;
  color: #ff0000;
  padding-left: 0.5em;
  margin-top: -5px;
  font-size: 0.8em; }

.mal-search-suggestion-image {
  height: 100px; }

.mal-search-suggestion span + span {
  margin-left: 2px; }

.mal-search-suggestion > button {
  height: 25px;
  justify-content: flex-start;
  align-items: flex-start;
  overflow: hidden; }

.mal-search-suggestion:global(.active) > button,
.mal-search-suggestion:hover > button {
  height: 110px;
  align-items: center; }
