.rating-control {
  position: relative;
  display: flex;
  padding: 5px;
    padding-top: 1em; }
  .rating-control .rating-control-label {
    position: absolute;
    top: 1px;
    left: 5px;
    font-size: 0.75em; }

.rating-control-option {
  height: 20px;
  width: 20px; }
  .rating-control-option input[type='radio'] {
    position: relative;
    appearance: none;
    height: 20px;
    width: 20px;
    margin: 0; }
    .rating-control-option input[type='radio']::before {
      content: '\2B50';
      position: absolute;
      top: -5px;
      bottom: 0;
      left: 0;
      right: 0;
      font-size: 1.5em; }

.rating-control:not(.read-only) .rating-control-option input {
  cursor: pointer; }

.rating-control-option.past input::before,
.rating-control-option.selected input::before {
  content: '\2605'; }

.rating-control-option.low:hover input::before {
  color: #100c08; }

.rating-control-option.low.past input::before,
.rating-control-option.low.selected input::before {
  color: #100c08; }

.rating-control-option.average:hover input::before {
  color: #cc6633; }

.rating-control-option.average.past input::before,
.rating-control-option.average.selected input::before {
  color: #cc6633; }

.rating-control-option.good:hover input::before {
  color: #cccccc; }

.rating-control-option.good.past input::before,
.rating-control-option.good.selected input::before {
  color: #cccccc; }

.rating-control-option.great:hover input::before {
  color: #ffcc00; }

.rating-control-option.great.past input::before,
.rating-control-option.great.selected input::before {
  color: #ffcc00; }
