.sidebar {
  position: fixed;
  top: 50px;
  height: 100%;
  width: 200px;
  background-color: #000;
  color: #ccc;
  z-index: 50; }
  .sidebar .sidebar-toggler {
    justify-content: flex-end;
    min-width: 40px;
    width: 100%;
    padding: 5px 20px;
    margin: 0; }
  .sidebar.collapsed {
    width: 50px; }
    .sidebar.collapsed .sidebar-toggler {
      justify-content: center; }
    .sidebar.collapsed .sidebar-item .item-text {
      display: none; }
    .sidebar.collapsed .sidebar-item .item-icon {
      display: flex;
      flex: 1 1 20%; }
  .sidebar.hidden {
    display: none; }

.sidebar-menu {
  padding: 0;
  margin: 0; }
  .sidebar-menu > .sidebar-item {
    width: 100%;
    height: 50px; }
    .sidebar-menu > .sidebar-item > a {
      min-width: 100%;
      width: 100%;
      height: 100%; }
    .sidebar-menu > .sidebar-item .item-text {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex: 1 1 80%;
      margin-left: 5px; }
    .sidebar-menu > .sidebar-item .item-icon {
      display: none; }
