/** 0BC SPINNER CSS **/
.spinner {
  width: 40px;
  height: 40px;
  clear: both;
  margin: auto; }

.spinner-circle {
  border: 4px rgba(255, 0, 0, 0.25) solid;
  border-top: 4px red solid;
  border-radius: 50%;
  -webkit-animation: circleRotate 0.6s infinite linear;
  animation: circleRotate 0.6s infinite linear; }

@-webkit-keyframes circleRotate {
  from {
    -webkit-transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(359deg); } }

@keyframes circleRotate {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(359deg); } }

/** MODERN SPINNER CSS */
.loader {
  position: relative;
  margin: 0px auto;
  width: 100px; }
  .loader:before {
    content: '';
    display: block;
    padding-top: 100%; }
  .loader .circular {
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }

.loader.control {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 30px; }

.loader.fullscreen {
  position: fixed;
  top: 50px;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #fcfcfc;
  background-color: rgba(240, 240, 240, 0.2);
  z-index: 999; }
  .loader.fullscreen .circular {
    top: 25vh;
    bottom: auto;
    left: 35vw;
    right: auto;
    width: 25vw;
    height: 25vh; }

.circular {
  animation: rotate 2s linear infinite;
  transform-origin: center center;
  position: absolute;
  margin: auto; }

.path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  stroke-linecap: round; }

@keyframes rotate {
  100% {
    transform: rotate(360deg); } }

@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0; }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px; }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px; } }

@keyframes color {
  100%,
  0% {
    stroke: #ff0000; }
  40% {
    stroke: #00ff00; }
  66% {
    stroke: #0000ff; }
  80%,
  90% {
    stroke: #ff0000; } }
