.chip-list-input-container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-start;
  align-items: flex-end;
  padding-top: 1em !important; }
  .chip-list-input-container .chip-list-clearable-input {
    padding-left: 0;
    padding-bottom: 0; }
  .chip-list-input-container .chip-list-wrapper {
    margin-bottom: 2px; }
  .chip-list-input-container .chip-list-inner {
    display: inline-flex;
    flex-wrap: wrap;
    min-height: 36px;
    max-width: 400px;
    padding: 0 2px;
    border: 0;
    border-radius: 0;
    border-bottom: 2px solid rgba(0, 0, 0, 0.1); }
