.clearable-input > input {
  display: flex;
  flex: 1 0 100%; }

.clearable-input:not(.not-clearable) > input {
  padding-right: 1.5em !important; }

.clearable-input .clear-input {
  position: relative;
  right: 30px; }

.clearable-input .clearable-input-count {
  position: absolute;
  right: 10px;
  bottom: -5px;
  top: auto;
  left: auto;
  font-size: 0.5rem; }

.clearable-input input[type='date']::-webkit-calendar-picker-indicator, .clearable-input input[type='date']::-webkit-inner-spin-button {
  appearance: none;
  display: none; }
