.tab-view {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  padding: 5px;
  margin: 0;
  opacity: 0;
  transition: all 1s ease-in-out;
  pointer-events: none; }
  .tab-view:global(.active) {
    position: relative;
    opacity: 1;
    z-index: 10;
    pointer-events: auto; }
