@charset "UTF-8";
.alert-container {
  position: fixed;
  top: 50px;
  left: 50%;
  width: 50%;
  height: 40px;
  transform: translateX(-50%);
  z-index: 1000; }
  .alert-container > .alert {
    width: 100%;
    background-color: #fff;
    color: #000;
    padding: 0;
    border-radius: inherit;
    margin: 0;
    box-shadow: 1px 1px 10px 1px;
    z-index: 10; }
    .alert-container > .alert .alert-title {
      display: flex;
      flex: 1;
      margin-left: 10px;
      font-size: 16px; }
    .alert-container > .alert :global(.button-group) {
      padding: 2px;
      margin: 0; }
      .alert-container > .alert :global(.button-group) button {
        min-width: auto; }

.close::before {
  font-size: 1rem; }

.alert-content {
  display: flex;
  flex-direction: column;
  height: 40px; }
  .alert-content .alert-top-content {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .alert-content .alert-top-content .alert-icon {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      color: #fff;
      padding: 0;
      font-size: 1.5rem;
      font-weight: bold; }
  .alert-content .alert-details {
    display: none;
    padding: 10px 0;
      padding-left: 5px;
    margin-left: 40px;
    white-space: pre-line;
    word-wrap: break-word; }
  .alert-content.is-expanded {
    height: auto; }
    .alert-content.is-expanded .alert-details {
      display: flex;
      width: auto;
      overflow: hidden; }

.alert.success .alert-icon {
  background-color: #00ff00;
  color: #000; }
  .alert.success .alert-icon::before {
    content: "✓"; }

.alert.error .alert-icon {
  background-color: #ff0000;
  color: #fff; }
  .alert.error .alert-icon::before {
    content: "!"; }

.alert.warning .alert-icon {
  background-color: #ff9900;
  color: #000; }
  .alert.warning .alert-icon::before {
    content: "⚠"; }

.alert.info .alert-icon {
  background-color: #00ffff;
  color: #000; }
  .alert.info .alert-icon::before {
    content: "ℹ"; }
